.sign-page
{
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 100vh;
    width: 100vw;    
    position: relative;
}

.logo{
    position: absolute;
    left: 3rem;
    margin-top: 1rem;
    z-index: 1000;
}

.logo h1{
    font-size: 1.5rem;
}

.sign-form
{
    width: 50%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.sign-form-content
{
    height: 60%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-evenly;
    text-align: center;
}

.sign-form-content form
{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}


.sign-form-content h1
{
    width: 12rem;
    font-size: 2rem;
    font-weight: 500;
}

.sign-form-content h4
{
    font-size: 1rem;
}


.signup-image{
    display: flex;
    justify-content: center;
    width: 50%;
    height: 100%;
}

.wallpaper-image1
{
    width: 100%;
    height: 100%;
    background-image: url('../assets/login_page/wallpaper.png');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

/* .wallpaper-image1 img
{
    height: 100%;
    width: 100%;
} */

.header__text{
    text-align:start;
}

.custom-input{
    display:flex;
    flex-direction:column;
    align-items: flex-start;
    border: 3px solid #ccc;
    /* margin:0px; */
    margin-bottom: 10px;
    /* padding:0px; */
    height:50px;
}

.custom-input input{
    /* outline:none; */
    border: none;
    width:100%;
    height:inherit;
}

.signup__form{
    display: flex;
    flex-direction: column;
    width: 400px;
}
.error{
    margin-top: 1rem;
    color:red;
    text-align:center;
    font-size: 15px;
    width: 80%;

}


.success{
    background-color:rgb(132, 236, 132);
    padding: 5px 10px;
    /* color: white; */
    margin-bottom: 1rem;
}


.bottom__text{
    width: 100%;
    margin-top: 1rem;
}

.bottom__text h5{
    width: 100%;
    padding:0px;
    margin:0px;
}

