.intl-tel-input{
    width: 100%;
    border: 1px solid #ccc;
    padding: 0.8rem 0px;
}

.intl-tel-input input{
    border: none;
    outline: none;
}

.phone__input{
    width: 100%;
    margin-bottom: 1rem;
}
.react-tel-input{
    width: 100%;
    border: 1px solid #ccc;
    padding: 0.6rem 0px;
    border-radius: 5px;
    
}
.react-tel-input:hover{
    border-color: black;
}

.react-tel-input input.form-control{
    border: none;
    outline: none;
    width: 100%;
    font-size: 1rem;
    height: 100%;
}

.react-tel-input input.form-control::placeholder{
    font-size:1rem;
}

.react-tel-input input.form-control:focus{
    border: none;
    outline: none;
}

.react-tel-input input.form-control:hover, .react-tel-input input.form-control:active{
    border: none;
    outline: none;
}


@media screen and (max-width: 950px){
    .sign-page{
        flex-direction: column-reverse;
        height:inherit;

        width:100%;
    }

    .sign-form-content h1
    {
        width:100%;
    }

    .signup-image{
        width:100%;
    }
    .wallpaper-image1{ 
        display: flex;
        justify-content:center; 
        align-items:center;
        height: 25vh; 
        width:85vw;
        border-bottom-left-radius: 1rem;
        border-bottom-right-radius: 1rem;
        overflow:hidden;    
        margin-bottom: 2rem;
    }
    .sign-form{ 
        width: 90%;
        height: 100%;
    }
    .logo{
        left:40%;
        color:white;
    }

    .sign-form-content h1
    {
        font-size: 1.6rem;
        font-weight: 500;
    }

    .bottom__text h5{
        font-size: 1rem;
    }

    .error{
        width:50%;
    }
}


@media screen and (max-width: 500px){
    .logo{
        left:33%;
    }
    .signup__form{
        width: 300px;
    }
}