.intl-tel-input{
    width: 100%;
    border: 1px solid #ccc;
    padding: 0.8rem 0px;
}

.intl-tel-input input{
    border: none;
    outline: none;
}

.phone__input{
    width: 100%;
    margin-bottom: 1rem;
}
.react-tel-input{
    width: 100%;
    border: 1px solid #ccc;
    padding: 0.6rem 0px;
    border-radius: 5px;
    
}
.react-tel-input:hover{
    border-color: black;
}

.react-tel-input input.form-control{
    border: none;
    outline: none;
    width: 100%;
    font-size: 1rem;
    height: 100%;
}

.react-tel-input input.form-control::-webkit-input-placeholder{
    font-size:1rem;
}

.react-tel-input input.form-control:-ms-input-placeholder{
    font-size:1rem;
}

.react-tel-input input.form-control::placeholder{
    font-size:1rem;
}

.react-tel-input input.form-control:focus{
    border: none;
    outline: none;
}

.react-tel-input input.form-control:hover, .react-tel-input input.form-control:active{
    border: none;
    outline: none;
}


