.landing-navbar
{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 10%;
    position: fixed;
    background: rgba(255,255,255, 0.4);
    padding:2% 2%;
}

.landing-navbar a
{
    color: black;
    text-decoration: none;
}

.landing-navbar h1
{
    cursor: pointer;
}

.landing-navbar li
{
    list-style-type: none;
    transition: 0.2s;
    cursor: pointer;
    height: 100%;
    width: 100%;
}

.links
{
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 30%;
}

.hamburger-menu
{
    display: none;
}

@media (max-width: 850px)
{

    .landing-navbar
    {
        padding: 2% 5%;
        z-index: 10;
    }

    .landing-navbar h1
    {
        cursor: pointer;
    }

    .links
    {
        display: none;
    }

    .hamburger-menu
    {
        display: flex;
        flex-direction: column;
        width: 35%;
        height: 100%; 
        transition: 0.3s;
    }

    .hamburger-menu i {
        cursor: pointer;
        display: flex;
        align-items: center;
        justify-content: center;
        height: 100%;
        width: 100%;
    }

    .nav-links
    {
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        align-items: center;
        height: auto;
        background-color: snow;
        border-radius: 10px;
        padding: 0 6%;
        width: 100%;
        z-index: 11;
    }

    #close-menu
    {
        width: 100%;
        height: 20%;
        display: flex;
        margin: 4%;
        justify-content: flex-end;
        align-items: center;
    }

    .nav-links a
    {
        text-decoration: none;
        color: black;
        width: 100%;
        height: 100%;
    }

    .nav-links div
    {
        width: 100%;
        border-radius: 10px;
        text-align: center;
        margin-bottom: 1rem;
        padding: 1rem;
        left: 2px;
        transition: 0.3s;
        cursor: pointer;
    }

    .nav-links div:active
    {
        background-color: #98a4cc;
        color: snow;
    }

    #blog-link:active
    {
        background-color: #98a4cc;
        color: snow;
    }
}
