*
{
    padding: 0%;
    box-sizing: border-box;
    scrollbar-width: none;
}

.landing-page
{
    min-height: 100vh;
    min-width: 100vw;
    height: auto;
    width: auto;
    scroll-behavior: smooth;
    font-family: -apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,"Helvetica Neue",Arial,"Noto Sans",sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol","Noto Color Emoji"
    
}

#section-1
{
    height: 100vh;
    width: 100vw;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.description
{
    height: 100%;
    width: 50%;
    flex-direction: column;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    padding: 3%;
}

.description h1
{
    font-size: 3rem;
    margin: 1rem;
    font-weight: 100;
    text-align: center;
}

.description p
{
    font-size: 1.2rem;
}

#section-1 .media-section
{
    height: 100%;
    width: 50%;
    background-image: url('https://skyhype.in/skyhype/Images/Web%201920%20%E2%80%93%203@2x_adobespark.png');
    background-size: cover;
    background-position: center;
}

#section-2
{
    height: 100vh;
    width: 100vw;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

#section-2 .media-section
{
    height: 100%;
    width: 50%;
    background-image: url('https://skyhype.in/skyhype/Images/Web%201920%20%E2%80%93%202@2x_adobespark.png');
    background-size: cover;
    background-position: center;
}

#section-3
{
    height: 100vh;
    width: 100vw;
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-between;
    align-items: center;
}

#section-3 .media-section
{
    height: 100%;
    width: 50%;
    background-image: url('https://skyhype.in/skyhype/Images/Web%201920%20%E2%80%93%201@2x_adobespark.png');
    background-size: cover;
    background-position: center;
}


#section-4
{
    height: 100vh;
    width: 100vw;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: white;
}

#section-4 .media-section
{
    height: 100%;
    width: 50%;
    background-image: url('https://skyhype.in/skyhype/Images/Group%2061%20new.png');
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
}

#section-5
{
    height: 100vh;
    width: 100vw;
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-between;
    align-items: center;
    background-color: white;
}

#section-5 .media-section
{
    height: 100%;
    width: 50%;
}

#section-5 img
{
    height: 100%;
    width: 100%;
}

#section-6
{
    height: 100vh;
    width: 100vw;
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-between;
    align-items: center;
    background-color: white;
}

#section-6 .media-section
{
    height: 100%;
    width: 50%;
}

#section-6 img
{
    height: 90%;
    width: 90%;
}

footer
{
    height: 40%;
    width: 100%;
}

#main-footer
{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

footer li
{
    list-style-type: none;
}

#download-section
{
    padding: 2% 0;
    display: flex;
    justify-content: center;
    align-items: center;
}

#social-section h3
{
    width: 100%;
    text-align: center;
}

.social-media-links
{
    width: 100%;
    display: grid;
    row-gap: 1rem;
    column-gap: 1rem;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(2,1fr);
    justify-content: center;
    align-content: center;
}

.social-media-links div
{
    background-color: black;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    width: 100%;
    padding: 0.5rem;
    transition: 0.3s;
}

.fa-brands
{
    font-size: 1.6rem;
    color: white;
    transition: 0.3s;
}

.fa-brands:hover
{
    color: black;
}

.social-media-links div:hover
{
    background-color: white;
    color: white;
}

#download-section h1
{
    margin: 0 1rem;
}


#download-section a
{
    margin: 0 1rem;
}

/*For Mobile and Tablets*/

@media (max-width: 850px)
{

    .description h1
    {
        font-size: 2rem;
    }

    .description p
    {
        font-size: 1rem;
    }

    #section-1
    {
        height: 100vh;
        width: 100vw;
        display: flex;
        flex-direction: column-reverse;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 15%;
}

    #section-2
    {
        height: 100vh;
        width: 100vw;
        display: flex;
        flex-direction: column-reverse;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 15%;
}

    #section-3
    {
        height: 100vh;
        width: 100vw;
        display: flex;
        flex-direction: column-reverse;
        justify-content: space-around;
        align-items: center;
        margin-bottom: 15%;
    }

    #section-4
    {
        min-width: 100vw;
        min-height: 100vh;
        display: flex;
        flex-direction: column-reverse;
        background-repeat: no-repeat;
        justify-content: space-between;
        align-items: center;
        /* margin-bottom: 15%; */
        padding: 1rem;
    }

    #section-4 .media-section 
    {
        height: 50%;
        width: 100%;
        background-size: contain;
    }

    #section-5
    {
        height: 100vh;
        width: 100vw;
        display: flex;
        flex-direction: column-reverse;
        justify-content: space-between;
        align-items: center;
    }

    #section-5 .media-section
    {
        width: 100%;
        height: 60%;
    }

    #section-6
    {
        height: 100vh;
        width: 100vw;
        display: flex;
        flex-direction: column-reverse;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 15%;
    }

    #section-6 .media-section
    {
        width: 100%;
        height: 60%;
    }

    .description
    {
        width: 100%;
        height: 30%;
    }

    .media-section
    {
        height: 70%;
        width: 100%;
    }

    #main-footer
    {
        display: flex;
        flex-direction: column;
        height: 100vh;
        width: 100vw;
        font-size: 1.4rem;
        justify-content: space-evenly;
        align-items: center;
    }

    #download-section
    {
        margin-bottom: 5%;
        flex-direction: column;
    }
}